import React, { useState, useEffect, useRef } from "react";
import { Box, Typography, Grid, Paper, Slide } from "@mui/material";

const MythSlide: React.FC = () => {
  const ref = useRef<HTMLDivElement | null>(null);
  const [checked, setChecked] = useState(false);

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        const entry = entries[0];
        setChecked(entry.isIntersecting); //  true false
      },
      { threshold: 0.2 } // 20% 
    );

    if (ref.current) observer.observe(ref.current);
    return () => observer.disconnect();
  }, []);

  const items = [
    {
      title: "",
      text: "",
    },
    {
      title: "",
      text: "    ",
    },
    {
      title: "",
      text: "",
    },
    {
      title: "",
      text: "",
    },
  ];

  return (
    <Box
      id="myth"
      ref={ref}
      sx={{
        minHeight: "100vh",
        backgroundImage:
          "url('https://images.unsplash.com/photo-1526403221644-0e3f4d92c30c?auto=format&fit=crop&w=1650&q=80')",
        backgroundSize: "cover",
        backgroundPosition: "center",
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center",
        px: 4,
        py: { xs: 6, md: 4 },
        position: "relative",
      }}
    >
      {/*  */}
      <Box
        sx={{
          position: "absolute",
          top: 0,
          left: 0,
          width: "100%",
          height: "100%",
          backgroundColor: "rgba(0,0,0,0.6)",
        }}
      />

      {/*  */}
      <Box
        sx={{
          position: "relative",
          zIndex: 1,
          color: "#fff",
          textAlign: "center",
          maxWidth: 1200,
        }}
      >
        <Typography variant="h2" fontWeight="bold" gutterBottom>
          
        </Typography>

        <Grid container spacing={4} justifyContent="center">
          {items.map((item, idx) => (
            <Grid item xs={12} md={6} key={idx}>
              <Slide direction="left" in={checked} timeout={1000}>
                <Paper
                  elevation={6}
                  sx={{
                    p: 3,
                    height: "100%",
                    backgroundColor: "rgba(255,255,255,0.1)", // 
                    color: "#fff", // 
                    borderRadius: 3,
                  }}
                >
                  <Typography variant="h5" fontWeight="bold" gutterBottom>
                    {item.title}
                  </Typography>
                  <Typography>{item.text}</Typography>
                </Paper>
              </Slide>
            </Grid>
          ))}
        </Grid>
      </Box>
    </Box>
  );
};

export default MythSlide;
