import React, { useEffect, useRef, useState } from "react";
import {
    Box,
    Typography,
    Grid,
    Card,
    CardContent,
    Slide,
    List,
    ListItem
} from "@mui/material";

const TypesSlide: React.FC = () => {
    const containerRef = useRef<HTMLDivElement | null>(null);

    const [firstChecked, setFirstChecked] = useState(false);
    const [secondChecked, setSecondChecked] = useState(false);
    const [thirdChecked, setThirdChecked] = useState(false);

    useEffect(() => {
        const observer = new IntersectionObserver(
            (entries) => {
                const entry = entries[0];

                if (entry.isIntersecting) {
                    const t1 = setTimeout(() => setFirstChecked(true), 0);
                    const t2 = setTimeout(() => setSecondChecked(true), 600);
                    const t3 = setTimeout(() => setThirdChecked(true), 1200);

                    return () => {
                        clearTimeout(t1);
                        clearTimeout(t2);
                        clearTimeout(t3);
                    };
                } else {
                    setFirstChecked(false);
                    setSecondChecked(false);
                    setThirdChecked(false);
                }
            },
            { threshold: 0.3 }
        );

        if (containerRef.current) observer.observe(containerRef.current);

        return () => observer.disconnect();
    }, []);

    return (
        <Box
            id="types"
            ref={containerRef}
            sx={{
                minHeight: "100vh",
                backgroundColor: "#1e1e1e",
                color: "#fff",
                display: "flex",
                flexDirection: "column",
                justifyContent: "center",
                alignItems: "center",
                px: 4,
                py: { xs: 6, md: 4 },
            }}
        >
            <Typography variant="h2" fontWeight="bold" gutterBottom>
                
            </Typography>

            <Grid container spacing={4} justifyContent="center">
                {/*  */}
                <Grid item xs={12} md={4}>
                    <Slide direction="left" in={firstChecked} timeout={800} mountOnEnter unmountOnExit>
                        <Card
                            sx={{
                                backgroundColor: "#2e2e2e",
                                color: "#fff",
                                borderRadius: 4,
                                boxShadow: 6,
                                p: 3,
                                cursor: "pointer",
                                height: 600,
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "center"
                            }}

                        >
                            <CardContent sx={{ width: "100%" }}>
                                <Typography variant="h4" fontWeight="bold" gutterBottom align="center">
                                    
                                </Typography>
                                <Typography variant="h6" align="center" paragraph>
                                    
                                </Typography>
                                <List dense>
                                    <ListItem> </ListItem>
                                    <ListItem> </ListItem>
                                    <ListItem> </ListItem>
                                    <ListItem> </ListItem>
                                    <ListItem> </ListItem>
                                </List>
                            </CardContent>
                        </Card>
                    </Slide>
                </Grid>

                {/*  */}
                <Grid item xs={12} md={4}>
                    <Slide direction="left" in={secondChecked} timeout={800} mountOnEnter unmountOnExit>
                        <Card
                            sx={{
                                backgroundColor: "#2e2e2e",
                                color: "#fff",
                                borderRadius: 4,
                                boxShadow: 6,
                                p: 3,
                                cursor: "pointer",
                                height: 600,
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "center"
                            }}

                        >
                            <CardContent sx={{ width: "100%" }}>

                                <Typography variant="h4" fontWeight="bold" gutterBottom align="center">
                                    
                                </Typography>
                                <Typography variant="h6" align="center" paragraph>
                                    
                                </Typography>
                                <List dense>
                                    <ListItem> </ListItem>
                                    <ListItem> </ListItem>
                                    <ListItem> </ListItem>
                                    <ListItem> </ListItem>
                                    <ListItem> </ListItem>
                                </List>

                            </CardContent>
                        </Card>
                    </Slide>
                </Grid>

                {/* DevOps /  */}
                <Grid item xs={12} md={4}>
                    <Slide direction="left" in={thirdChecked} timeout={800} mountOnEnter unmountOnExit>
                        <Card
                            sx={{
                                backgroundColor: "#2e2e2e",
                                color: "#fff",
                                borderRadius: 4,
                                boxShadow: 6,
                                p: 3,
                                cursor: "pointer",
                                height: 600,
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "center"
                            }}
                        >
                            <CardContent sx={{ width: "100%" }}>
                                <Typography variant="h4" fontWeight="bold" gutterBottom align="center">
                                    DevOps / 
                                </Typography>
                                <Typography variant="h6" align="center" paragraph>
                                    
                                </Typography>
                                <List dense>
                                    <ListItem> </ListItem>
                                    <ListItem> </ListItem>
                                    <ListItem> </ListItem>
                                    <ListItem> </ListItem>
                                    <ListItem> </ListItem>
                                </List>
                            </CardContent>
                        </Card>
                    </Slide>
                </Grid>
            </Grid>
        </Box>
    );
};

export default TypesSlide;
